import React, { Component } from 'react';
import Carousel from 'react-bootstrap/Carousel';
import forestImg from '../assets/pexels-eberhard-grossgasteiger-1004665.jpg';
import dforestImg from '../assets/pexels-mengliu-di-3064079.jpg';

export default class carouselBox extends Component {
    render() {
        return (
            <Carousel>
                <Carousel.Item>
                    <img
                        className="d-block w-100"
                        src={forestImg}
                        alt="Forest"
                    />
                    <Carousel.Caption>
                        <h3>Forest image</h3>
                        <p>Forest image Forest image Forest image</p>
                    </Carousel.Caption>
                </Carousel.Item>
                <Carousel.Item>
                    <img
                        className="d-block w-100"
                        src={dforestImg}
                        alt="Forest 2"
                    />
                    <Carousel.Caption>
                        <h3>Forest image 2</h3>
                        <p>Forest image Forest image Forest image</p>
                    </Carousel.Caption>
                </Carousel.Item>
            </Carousel>
        )
    }
}
